<template>
	<div class="app-container">
		<p>
			<el-col :span="8">
				选择电信角色:
				<el-select v-model="value"  placeholder="请选择" @change="companyChange">
					<el-option v-for="item in options" :key="item.ctId" :label="item.ctName" :value="item.ctId">
					</el-option>
				</el-select>
			</el-col>
			<el-col :span="8">
				<el-date-picker v-model="month" filters type="month" format="yyyy-MM" @change="dateChange" placeholder="选择月" :clearable="false">
				</el-date-picker>
			</el-col>
		</p>
		<el-table :data="tableData" border style="width: 100%" v-loading="isLoad" >
			<el-table-column prop="ctInfoDO.ctId" label="客户编号" width="150" align="center">
			</el-table-column>
			<el-table-column prop="ctInfoDO.name" label="客户名称" width="100" align="center">
			</el-table-column>
			<el-table-column prop="contractNumber" label="合同编号" width="150" align="center">
			</el-table-column>
			<el-table-column prop="time" label="收入年月" width="150" align="center">
			</el-table-column>
			<el-table-column prop="packagePrice" label="流量包成本(元)" align="center">
			</el-table-column>
			<el-table-column prop="ctRatio" label="佣金比例" align="center">
			</el-table-column>
			<el-table-column prop="platformCommision" label="佣金收入(元)" width="90" align="center">
			</el-table-column>
		</el-table>
		<div style="text-align:center;margin: 20px auto;">
			<el-pagination style="display: inline-block" :page-size="10" @current-change="pageChange" :current-page.sync="currentPage" layout="prev, pager, next, jumper" :total="tableCount">
			</el-pagination>
		</div>
	</div>
</template>

<script>
import {
    getListCTInfoGeneral,
    giveCTAuthority
} from 'api/admin/usual';
import {
	getListRechargeCommisionByManager
} from 'api/admin/financialSettlement';

import { backCN } from 'utils/index';
export default {
	data() {
		return {
			options: [],
			value: '',
			tableData: [],
			month: '2017-06',
			currentMonth:'201706',
			currentPage: 1,
			tableCount: 0,
			num: 10,
			isLoad:true
		}
	},
	watch: {
		options(){
			this.value = this.options[0].ctId
		}
	},
	mounted() {
	this.initHeader()
	},
	methods: {
		initHeader(){
	this.isLoad=true
		getListCTInfoGeneral()
			.then(res => {
				res.msg === 0?this.options = res.ctList:this.$message.error(backCN(res.code))
				res.msg!=0 ?this.isLoad=false:''	
			}).catch(err=>{
				this.isLoad=false;
				this.$message.error(backCN(err.status))
			});
		},
		dateChange(val) {
			this.currentMonth= val.split('-').join('')
			this.ListRechargeCommisionByManager(this.currentMonth, 1, this.num)
		},
		companyChange(ctId){
			 this.CTInfo(ctId)
		},
		pageChange(page) {
			this.ListRechargeCommisionByManager(this.currentMonth, page, this.num)
		},
		ListRechargeCommisionByManager(month,  page, num) {
			this.isLoad=true
			console.log(2222)
			getListRechargeCommisionByManager({ month, page, num })
				.then(res => {
					this.isLoad=false
					if (res.msg == 0) {
						console.log("res", res)
						this.tableData = res.commisionList
						page===1?this.tableCount = res.count:''
					} else {
						this.tableData = []
						this.tableCount = 0
					}
					(res.msg != 0 && res.msg != 1) ? this.$message.error(backCN(res.code)) : ''
				});
		},
		 CTInfo(ctId) {
            this.isLoad=true
            giveCTAuthority({ ctId })
                .then(res => {
                    this.isLoad=false
                    res.msg === 0?this.ListRechargeCommisionByManager(this.currentMonth,  1, this.num)
                    :this.$message.error(backCN(res.code))
                });
        }
	}
};
</script>

<style scoped>
.app-container {
	background: #f5f5f5;
}
</style>


